html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  line-height: 1.5;
  font-family: "Roboto", sans-serif;
  background-color: hsl(234, 29%, 20%);
}

p {
  margin: 0;
}

a {
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.contenedor {
  margin: 0 auto;
  max-width: 120rem;
  width: 90%;
  grid-column: 1/2;
  grid-row: 1/2;
}

.attribution {
  font-size: 14px;
  text-align: center;
  background-color: hsl(0, 0%, 100%);
  margin: 0;
  padding: 1rem 0;
}

.attribution a {
  color: hsl(228, 45%, 44%);
}

.opacity-50 {
  opacity: 0;
}

.mensaje-exito {
  width: 100%;
  padding: 1.5rem;
  border-radius: 0.5rem;
  background-color: rgb(137, 196, 49);
  color: hsl(0, 0%, 100%);
  text-align: center;
  margin-top: 1rem;
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .margin {
    display: flex;
    justify-content: center;
    margin: 10rem 0;
  }
}

.card {
  background-color: hsl(0, 0%, 100%);
  padding-bottom: 2rem;
}
@media (min-width: 768px) {
  .card {
    max-width: 90rem;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    border-radius: 2rem;
    padding: 2rem;
  }
}
.card__img {
  background-image: url(../img/illustration-sign-up-mobile.svg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  height: 20rem;
  width: 100%;
  border-radius: 0 0 2rem 2rem;
  grid-column: 2/2;
}
@media (min-width: 768px) {
  .card__img {
    background-image: url(../img/illustration-sign-up-desktop.svg);
    height: 50rem;
    border-radius: 2rem;
  }
}
@media (min-width: 768px) {
  .card__heading {
    font-size: 5rem;
  }
}

.lista {
  list-style: none;
  padding: 0;
}
.lista__li {
  display: flex;
  gap: 2rem;
  align-items: center;
  margin-bottom: 1rem;
}
.lista__span {
  width: 3rem;
}

.form__flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.form__label {
  display: block;
  font-size: 1.3rem;
  margin-bottom: 0.5rem;
}
.form__label--error {
  color: red;
}
.form__email, .form__submit {
  padding: 1.5rem;
  border-radius: 0.5rem;
  width: 100%;
}
.form__email {
  font-size: 1.4rem;
  color: hsl(231, 7%, 60%);
  border: 1px solid hsl(234, 29%, 20%);
  margin-bottom: 1rem;
}
.form__email.mostrar-error {
  border: 1px solid red;
  background-color: #ffd6d6;
  color: red;
}
.form__submit {
  text-align: center;
  background-color: hsl(234, 29%, 20%);
  border: none;
  color: hsl(0, 0%, 100%);
}
.form__submit:hover {
  cursor: pointer;
  background-image: linear-gradient(to right, hsl(4, 100%, 67%) 0%, #ff3224 100%);
}

.success {
  background-color: hsl(0, 0%, 100%);
  height: 100vh;
  padding: 5rem 2rem;
}
@media (min-width: 768px) {
  .success {
    max-width: 55rem;
    padding: 6rem;
    height: auto;
    border-radius: 3rem;
  }
}
.success__img {
  margin-bottom: 3rem;
}
.success__heading {
  font-size: 4rem;
  margin: 0;
  font-weight: 900;
}
@media (min-width: 768px) {
  .success__heading {
    font-size: 6rem;
    line-height: 1;
    padding-bottom: 2rem;
  }
}
.success__parrafo {
  color: hsl(234, 29%, 20%);
  margin-top: 2rem;
}
.success__parrafo--dismiss {
  text-align: center;
  background-color: hsl(234, 29%, 20%);
  color: hsl(0, 0%, 100%);
  padding: 1.5rem;
  border-radius: 0.5rem;
  margin-top: 4rem;
  border: none;
  width: 100%;
}
.success__parrafo--dismiss:hover {
  cursor: pointer;
  background-image: linear-gradient(to right, hsl(4, 100%, 67%) 0%, #ff3224 100%);
}
.success__span {
  font-weight: bold;
}

.spinner {
  margin: 50px auto 0;
  width: 70px;
  text-align: center;
  display: none;
}

.spinner > div {
  width: 18px;
  height: 18px;
  background-color: #333;
  border-radius: 100%;
  display: inline-block;
  -webkit-animation: sk-bouncedelay 1.4s infinite ease-in-out both;
  animation: sk-bouncedelay 1.4s infinite ease-in-out both;
}

.spinner .bounce1 {
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}

.spinner .bounce2 {
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}

@-webkit-keyframes sk-bouncedelay {
  0%, 80%, 100% {
    -webkit-transform: scale(0);
  }
  40% {
    -webkit-transform: scale(1);
  }
}
@keyframes sk-bouncedelay {
  0%, 80%, 100% {
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  40% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}
.mostrar-spinner {
  display: block;
}